@import url("https://fonts.googleapis.com/css2?family=Dancing+Script:wght@500&display=swap");

html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
body {
  margin: 7px;
  padding: 9px;
  line-height: 3;
  font-family: "Dancing Script", cursive;
  color: rgb(80, 69, 52);
  font-size: 1.3rem;

  background-image: url("https://getwallpapers.com/wallpaper/full/c/8/1/31043.jpg"),
    url("https://i.pinimg.com/originals/7e/81/be/7e81be3b1deb1d8e9e95c00da92df52c.gif");
  background-blend-mode: multiply;
}
header {
  background-color: aqua;
}
.header {
  border-bottom: 4px solid rgb(241, 188, 196);
  box-shadow: 8px 12px 0 0 rgba(223, 60, 136, 0.7);
  width: 100%;
  height: 100px;

  nav {
    max-width: 922px;
    display: flex;
    align-items: center;
    margin: auto;
    .logo {
      /* background-color: antiquewhite; why are colors not showing */
      float: right;
      #logo-img {
        width: 90px;
        box-shadow: 4px 4px 5px 0 rgb(187, 134, 143);
        position: absolute;
        top: 19px;
        right: 130px;
      }
      .Name {
        display: inline;
        float: left;
        font-weight: 800;
        font-size: 1.2em;
        position: absolute;
        top: 29px;
        right: 1560px;
      }
    }
  }
}
.content {
  display: flex;
  max-width: 822px;

  margin: 149px;
  margin-left: 300px;
  z-index: -9;
  .sidebar {
    /* position: fixed;*/
    font-weight: 1000;
    margin-left: 330px;
    color: rgb(193, 205, 216);
    user-select: none;
    cursor: pointer;
    line-height: 139px;
    height: 50vh;
    div {
      margin-bottom: 5px;
      &.active {
        font-size: 1.7rem;
        font-weight: 900;
        color: white;
      }
      .icon {
        margin-left: 38px;
      }
    }
  }

  background: linear-gradient(
    to right,
    rgba(172, 233, 94, 0.9),
    rgba(206, 183, 52, 0.3),
    rgba(3, 4, 5, 0.7)
  );

  width: 100%;
  height: 100%;
}
.tasks {
  margin-left: 70px;
  font-size: medium;
}
#myTasks {
  font-size: 1.12rem;
  font-weight: 600;
}
.add-task-text {
  font-weight: 800;
  text-decoration: underline;
  font-size: 1.23rem;
}
button {
  border: none;
  background: linear-gradient(to left, rgba(249, 149, 252, 0.7), orange);

  margin-right: 25px;
  color: rgba(44, 33, 44, 1);
  font-weight: bold;
  height: 32px;
  border-bottom: 3px dashed rgb(70, 66, 57);
  border-radius: 12px;
}
button.add-btn:hover {
  width: 39px;
  font-size: 0.7rem;
  transition: 0.8s;
}
button.cancel-btn:hover {
  width: 65px;
  font-size: 0.7;
  transition: 0.7s;
}
input {
  border: none;

  background-color: rgba(0, 0, 0, 0.7);
  border-radius: 14px;
  text-align: center;
  color: aliceblue;
  height: 33px;
  font-style: oblique;
  font-weight: 500;
}
.add-btn {
  &:active {
    background-color: yellowgreen;
  }
  &:disabled {
    background-color: gray;
  }
}
.task-items-container {
  margin-top: 3px;
  padding: auto;
  .task-item {
    display: flex;
    justify-content: space-between;
    &:not(:last-child) {
      border-bottom: 2px solid yellow;
    }
  }
}
.note-form {
  position: fixed;
  left: 1000px;
  top: 23px;
  /* display: flex;
  flex-direction: row;*/
  max-width: 100%;
  max-height: 100%;
  textarea {
    padding: 10px;
    outline: none;
    border: none;
    border-radius: 6px;
    width: 356px;
    height: 78px;
  }
  button {
    margin-left: 30px;
    position: fixed;
    top: 50px;

    outline: none;
    background-color: beige;
    &:hover {
      background-color: tomato;
      color: white;
    }
  }
}
.note {
  position: absolute;
  cursor: pointer;
  background-image: url("https://th.bing.com/th/id/R.d2489c52612bbda488eff80b33a6666a?rik=oS7qZGs14y25gQ&riu=http%3a%2f%2fpngimg.com%2fuploads%2fsticky_note%2fsticky_note_PNG18952.png&ehk=T2Gry9UpjfliVQ54faP09yJ5beWYLBBCYvtd1KiVCFc%3d&risl=&pid=ImgRaw&r=0");
  background-size: cover;
  top: 380px;
  left: 1400px;
  height: 200px;

  width: 200px;
  padding: 20px;
  color: brown;
  text-align: justify;
  .text {
    font-family: sans-serif;
    height: 100%;
    width: 100%;
    white-space: pre-wrap;
    //overflow-y: scroll;
    overflow: hidden;
  }
}
.close {
  width: 15px;
  height: 15px;
  position: absolute;
  right: 15px;
  transition: transform 0.3s ease;
  &:hover {
    transform: scale(1.6);
  }
}
.sticky {
  position: fixed;
  top: 0;
  left: 600px;
  display: inline;
  z-index: 88;
}
